import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import withStyles from "@material-ui/core/styles/withStyles";

// Material UI
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import InputBase from '@material-ui/core/InputBase';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '@material-ui/icons/Search';
import RefreshIcon from '@material-ui/icons/Refresh';

// Components
import Profile from "./Profile";
import MenuButton from "./menuButton";

const outline = {
  border: "1px solid #dadce0",
  borderRadius: 48,
  padding: "5px 15px",
}
// Theme spread is from "../utils.theme"
const styles = (theme) => ({
  ...theme.custom,
  title: {
    flexGrow: 1,
  },
  appBar: {
    marginTop: theme.spacing(10),
    marginBottom: theme.spacing(2),
    fontWeight: 400,
    display: "block",
    backgroundColor: "#fff",
    color: "#888",
    borderBottom: "1px solid #e4e6e7",
  },
  logo: {
    height: 40,
    maxWidth: 160,
    verticalAlign: "bottom",
  },
  toolbar: {
    ...outline,
    display: 'flex',
    flexDirection: 'row',
    minHeight: 0,
    justifyContent: "flex-start",
    left: 0,
    margin: "20px 0 10px 0",
    overflowX: "hidden",
    position: "relative",
    right: 0
  },
  rounded: {
    ...outline,
    display: "inline-block",
    padding: 0,
    overflow: "hidden",
    marginRight: 10
  },
  iconSearch: {
    marginRight: theme.spacing(0.5),
  },
  search: {
    borderRight: "1px solid #dadce0",
    flex: 1,
  },
  input: {
    padding: `0 ${theme.spacing(3)}px`,
    borderRight: "1px solid #dadce0",
  },
  iconButton: {
    padding: theme.spacing(1),
    marginLeft: theme.spacing(0.5),
  },
});

export class DoctorFilters extends Component {
  render() {
    const { classes } = this.props;

    return (
      <div>
        <Toolbar className={classes.toolbar}>
            <InputBase
            fontSize={14}
            className={classes.search}
            color={'secondary'}
            placeholder={'Search'}
            startAdornment={<SearchIcon className={classes.iconSearch} /> }
            />
            <div className={classes.input}>
              <MenuButton
              size="large"
              title="Location"
              items={['Profile','User Management', 'Logout']}
              />
            </div>
            <IconButton className={classes.iconButton} aria-label="search">
              <RefreshIcon />
            </IconButton>
        </Toolbar>
        
        <div className={classes.rounded}>
          <MenuButton
          size="large"
          title="Services"
          items={['Profile','User Management', 'Logout']}
          />
        </div>
        <div className={classes.rounded}>
          <MenuButton
          size="large"
          title="Department"
          items={['Profile','User Management', 'Logout']}
          />
        </div>
        <div className={classes.rounded}>
          <MenuButton
          size="large"
          title="Slots"
          items={['Profile','User Management', 'Logout']}
          />
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(DoctorFilters);
