import React, { Component } from "react";
import Grid from "@material-ui/core/Grid";
import propTypes from "prop-types";

// Components
import Doctor from "../components/Doctor";
import DoctorFilters from "../components/doctorFilters";

// Redux
import { connect } from "react-redux";
import { getDoctors } from "../redux/actions/dataActions";
import { Typography } from "@material-ui/core";

export class doctors extends Component {
  componentDidMount() {
    this.props.getDoctors();
  }

  render() {
    const { doctors, loading } = this.props.data;
    const count = this.props.data.doctors.length;

    return (
      <React.Fragment>
        <DoctorFilters/>
        {loading ? <p>loading....</p> : (
          <div>
            <Typography color="textSecondary" className="mt2 mb2">
              {count} of {count} doctors
            </Typography>
            <Grid container spacing={2}>
              { doctors.map((doctor) => (
                <Grid item sm={6} xs={12} key={doctor.doctorId}>
                  <Doctor doctor={doctor} key={doctor.doctorId} />
                </Grid>
              )) }
            </Grid>
          </div>
        )}
      </React.Fragment>
    );
  }
}

doctors.propTypes = {
  getDoctors: propTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  data: state.data,
});

export default connect(mapStateToProps, { getDoctors })(doctors);
